<ul class="projects"><li class="projects__item" v-for="item in projects" :key="item.id"><div class="projects__imgWrapper"><img class="projects__img" :src="item.img" alt="alt" loading="lazy"></div><div class="projects__content"><span class="projects__name">{{item.name}}</span><h3 class="projects__title">{{item.title}}</h3><span class="projects__url">{{item.url}}</span></div></li></ul>