<div class="header container" v-motion :initial="{ opacity: 0, y: 100 }" :enter="{ opacity: 1, y: 0, scale: 1 }" :variants="{ custom: { scale: 0 } }" :delay="2000"><div class="header__Wrapper"><div class="header__top"><div class="header__info"><span class="header__contact">+49 89 64 19 280</span><a class="header__contact">info@bhb-bayern.de</a></div><div class="header__logo"><img class="header__img" :src="logo" loading="lazy" alt="logo"></div><div class="header__socialMedia"><a class="header__contact">Facebook</a><a class="header__contact">Instagram</a></div></div><ul class="header__menu"><li class="header__item" v-for="item in navItem" :key="item.id"><a class="header__link" href="javascript:void(0)">{{item.text}}</a></li></ul></div></div>